.section__title {
  margin-bottom: 16px;
}

.section__description {
  margin-bottom: 32px;
}

.section__image {
  width: 100%;
  height: 577px;
}

.section__details {
  margin-bottom: 24px;
  display: flex;
  gap: 16px;
}

.section__advantages, .section__features {
  width: 50%;
  padding: 12px 16px;
  border-radius: 2px;
  background-color: #EFEFEF;
}

.section__details .advantages__list .advantages__item {
  list-style: disc inside;
}

.section__details .features__list .features__item {
  list-style: disc inside;
}